import { SetupMachineContext, SetupMachineEvents } from "machines/setup.machine";
import { State } from "xstate";

export const StepCloning = ({
  state,
}: {
  state: State<SetupMachineContext, SetupMachineEvents, any, any>;
}) => {
  return (
    <>
      {state.matches("stepCloning.start") && <div>Cloning</div>}
      {state.matches("stepCloning.done") && <div>Cloned</div>}
    </>
  );
};
