import axios from "axios";

interface RepositoryInformations {
  url: string;
  branch: string;
}

export interface Configuration {
  sourceRepository: RepositoryInformations;
  publicationRepository: RepositoryInformations;
}

export const validateConfiguration = (configuration: any): Configuration | undefined =>
  configuration.sourceRepository?.url &&
  configuration.sourceRepository?.branch &&
  configuration.publicationRepository?.url &&
  configuration.publicationRepository?.branch
    ? (configuration as Configuration)
    : undefined;

export const fetchRepositoryConfiguration = async (): Promise<Configuration> => {
  const resp = await axios.get("./config.json");

  const configuration = validateConfiguration(resp.data);

  if (!configuration) {
    throw new Error(`The configuration is not valid: ${JSON.stringify(resp.data)}`);
  }

  return configuration;
};

export const defaultConfiguration = ({ url }: { url: string }): Configuration => {
  return {
    sourceRepository: {
      url,
      branch: "main",
    },
    publicationRepository: {
      url,
      branch: "main",
    },
  };
};
