/**
 *
 * We want to select the provider at BUILD time
 * So we can't use CONST for provider nor switch instead of if
 * If that everything will be bundled no matter the value of PROVIDER
 * and hte import will be done at RUNTIME
 *
 * Keep literals and if :)
 */

type getProfile = (data: { username: string; personalToken: string; password: string }) => string;

export interface Provider {
  getProfile: getProfile;
  infos: { name: string };
  isAuthorizedToPush: boolean;
}

let provider: Provider;

if (process.env.REACT_APP_PROVIDER === "github") {
  provider = require(`./github`);
}

if (process.env.REACT_APP_PROVIDER === "gitlab") {
  provider = require(`./gitlab`);
}

if (!process.env.REACT_APP_PROVIDER) {
  throw new Error("REACT_APP_PROVIDER is not defined");
}
// @ts-ignore
export default provider;
