import { useService } from "@xstate/react";
import { Link } from "react-router-dom";
import "./App.css";
import provider from "./providers";
import "twin.macro";
import "./machines/inspector";
import { setupService } from "machines/setup.machine";
import { SetupContainer } from "pages/setup/SetupContainer";

let test = () => {};
console.error(provider.infos, process.env.NODE_ENV, process.env.REACT_APP_TEST);

test();

const Home = () => (
  <h1>
    Home
    <div>
      <button onClick={() => console.log("clicked")}>Load</button>
    </div>
  </h1>
);

function App() {
  const [setupState] = useService(setupService);

  const isSetupInitialized = setupState.matches("initialized");
  const isAuthenticated = false;

  switch (true) {
    case !isSetupInitialized:
      return <SetupContainer setupService={setupService} />;

    case !isAuthenticated:
      return <div>Need Auth {JSON.stringify(setupState.context)}</div>;

    default:
      return (
        <>
          <Home />
          <div>
            <Link style={{ marginRight: "10px" }} to="/">
              Home
            </Link>
            <Link style={{ marginRight: "10px" }} to="/todos">
              Todos
            </Link>
            <Link style={{ marginRight: "10px" }} to="/not-foudofudfosuf">
              Lost
            </Link>
            <Link style={{ marginRight: "10px" }} to="/sign-in">
              Sign-in
            </Link>
          </div>
        </>
      );
  }
}

export default App;
