import { useService } from "@xstate/react";
import { SetupMachineContext, SetupMachineEvents } from "machines/setup.machine";
import { Interpreter } from "xstate";
import { StepVault } from "./StepVault";
import tw from "twin.macro";
import { StepRepository } from "./StepRepository";
import { StepCloning } from "./StepCloning";
import { SetupWelcome } from "./SetupWelcome";

export interface Props {
  setupService: Interpreter<SetupMachineContext, any, SetupMachineEvents, any>;
}

const Header = ({ title, profile }: any) => {
  return (
    <div css={[tw`bg-red-200`]}>
      <span>{title}</span>

      {profile && <img src={profile.avatar_url} alt="" />}
    </div>
  );
};

export const SetupContainer = ({ setupService }: Props) => {
  const [setupState, sendSetup] = useService(setupService);

  const handleWelcomeSubmit = () => sendSetup({ type: "SUBMIT_WELCOME" });
  const handleAuthenticationSubmit = (payload: any) =>
    sendSetup({ type: "SUBMIT_CREDENTIALS", ...payload });

  const handleRepositorySubmit = (payload: any) =>
    sendSetup({ type: "SUBMIT_REPOSITORY", ...payload });

  return (
    <>
      <div css={[tw`flex flex-col h-screen`]}>
        <Header
          title={setupState.matches("stepAuthentication") ? "Authentication" : "Repository"}
          profile={setupState.context.profile}
        />
        <div css={[tw`flex-1 bg-green-400`]}>
          {["welcomeInitializeApplication", "welcomeInitializeVaultOnly"].some(
            setupState.matches
          ) && <SetupWelcome onSubmit={handleWelcomeSubmit} />}
          {setupState.matches("initializeVault") && (
            <StepVault error={setupState.context.error} onSubmit={handleAuthenticationSubmit} />
          )}
          {setupState.matches("initializeRepositories") && (
            <StepRepository error={setupState.context.error} onSubmit={handleRepositorySubmit} />
          )}
          {setupState.matches("stepCloning") && <StepCloning state={setupState} />}
        </div>
      </div>
    </>
  );
};
