import tw from "twin.macro";
import { useTranslation } from "react-i18next";
import { useForm } from "react-hook-form";
import { Button } from "components/Button";

const Wrapper = tw.div`h-full w-full flex justify-center items-center`;

const FormWrapper = tw.form`flex flex-col justify-center items-center max-w-sm`;

const Title = tw.h1`text-2xl font-bold`;

const Label = tw.label`mt-2`;
const Input = tw.input`w-full border-2 border-gray-300 `;

export interface Props {
  onSubmit: (data: any) => void;
  error: string | undefined;
}

interface Inputs {
  repositoryUrl: string;
}

export const StepRepository = ({ onSubmit, error }: Props) => {
  const { t } = useTranslation();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<Inputs>();
  return (
    <Wrapper>
      <FormWrapper onSubmit={handleSubmit(onSubmit)}>
        <Title>Repository</Title>

        {error && <div css={[tw`px-6 py-2 bg-red-200`]}>{t(error)}</div>}

        <Label htmlFor="repositoryUrl">URL of your repository</Label>
        <Input
          id="repositoryUrl"
          placeholder="https://gitlab.com/sdfsf"
          {...register("repositoryUrl", { required: true })}
        />
        {errors.repositoryUrl && <span>This field is required</span>}

        <Button css={[tw`mt-8`]} variant="primary" type="submit">
          Ok
        </Button>
      </FormWrapper>
    </Wrapper>
  );
};
