import tw from "twin.macro";
import { useTranslation } from "react-i18next";
import { useForm } from "react-hook-form";
import { Button } from "components/Button";

const Wrapper = tw.div`h-full w-full flex justify-center items-center`;

const FormWrapper = tw.form`flex flex-col justify-center items-center max-w-sm`;

const Title = tw.h1`text-2xl font-bold`;

const Label = tw.label`mt-2`;
const Input = tw.input`w-full border-2 border-gray-300 `;

export interface Props {
  onSubmit: (data: any) => void;
  error: string | undefined;
}

interface Inputs {
  username: string;
  token: string;
  password: string;
}

export const StepVault = ({ onSubmit, error }: Props) => {
  const { t } = useTranslation();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<Inputs>();
  return (
    <Wrapper>
      <FormWrapper onSubmit={handleSubmit(onSubmit)}>
        <Title>Authentication</Title>

        {error && <div css={[tw`px-6 py-2 bg-red-200`]}>{t(error)}</div>}

        <Label htmlFor="username">Username</Label>
        <Input
          id="username"
          placeholder="Your username"
          {...register("username", { required: true })}
        />
        {errors.username && <span>This field is required</span>}

        <Label htmlFor="token">Personal Token</Label>
        <Input id="token" {...register("token", { required: true })} />
        {errors.token && <span>This field is required</span>}

        <Label htmlFor="password">Password</Label>
        <Input id="password" type="password" {...register("password", { required: true })} />
        {errors.password && <span>This field is required</span>}

        <Button css={[tw`mt-8`]} variant="primary" type="submit">
          Ok
        </Button>
      </FormWrapper>
    </Wrapper>
  );
};
