import log from "loglevel";
import { getVault } from "services/authentication/localStorageAuthentication";
import {
  defaultConfiguration,
  fetchRepositoryConfiguration,
} from "services/configuration/remoteConfiguration";
import { assign, interpret, Machine } from "xstate";
import { Configuration } from "../services/configuration/remoteConfiguration";
import { getProfile, isAuthorizedToPush } from "providers/gitlab";
import {
  cloneRepositores,
  getLocalRepositoriesConfiguration,
  initializeDirectories,
} from "services/repository/localRepository";

export interface SetupMachineSchema {
  states: {
    initializingDirectories: {};
    checkingGlobalSetup: {
      states: {
        checkingRemoteConfiguration: {
          states: {
            checking: {};
            done: {};
          };
        };
        checkingLocalVault: {
          states: {
            checking: {};
            done: {};
          };
        };
      };
    };
    dealWithPossibilities: {};
    welcomeInitializeApplication: {};
    welcomeInitializeVaultOnly: {};
    initializeVault: {
      states: {
        idle: {};
        authenticating: {};
        validatingAuthorization: {};
        authenticated: {};
      };
    };
    initializeRepositories: {
      states: {
        checkingIfGoNext: {};
        idle: {};
        validateRepository: {};
        cloneRespositories: {
          states: {
            start: {};
            done: {};
          };
        };
        done: {};
      };
    };
    allGood: {};
  };
}

export type SetupMachineEvents =
  | { type: "SUBMIT_WELCOME" }
  | { type: "SUBMIT_CREDENTIALS" }
  | { type: "SUBMIT_REPOSITORY" }
  | { type: "CHECK_AUTHORIZATION_IF_CONFIGURED" }
  | { type: "CHECK_IF_STEP_REPOSITORY_REQUIRED" };

export interface SetupMachineContext {
  error: string | undefined;
  remoteConfiguration: Configuration | undefined;
  vault: any;
  credentials: any;
  profile: any;
}

export const setupMachine = Machine<SetupMachineContext, SetupMachineSchema, SetupMachineEvents>(
  {
    id: "setup",
    initial: "initializingDirectories",
    context: {
      error: undefined,
      remoteConfiguration: undefined,
      vault: undefined,
      credentials: undefined,
      profile: undefined,
    },
    states: {
      initializingDirectories: {
        invoke: {
          src: "initializeDirectories",
          onDone: { target: "checkingGlobalSetup" },
          onError: { target: "checkingGlobalSetup" },
        },
      },
      checkingGlobalSetup: {
        type: "parallel",
        states: {
          checkingRemoteConfiguration: {
            initial: "checking",
            states: {
              checking: {
                invoke: {
                  src: "checkRemoteConfiguration",
                  onDone: { target: "done", actions: "setRemoteConfiguration" },
                  onError: { target: "done" },
                },
              },
              done: {
                type: "final",
              },
            },
          },
          checkingLocalVault: {
            initial: "checking",
            states: {
              checking: {
                invoke: {
                  src: "checkVault",
                  onDone: { target: "done", actions: "setVault" },
                  onError: { target: "done" },
                },
              },
              done: {
                type: "final",
              },
            },
          },
          // checkingLocalRepositoriesConfiguration: {
          //   initial: "checking",
          //   states: {
          //     checking: {
          //       invoke: {
          //         src: "checkLocalRepositoriesConfiguration",
          //         onDone: { target: "done", actions: "setLocalRepositoriesConfiguration" },
          //         onError: { target: "done" },
          //       },
          //     },
          //     done: {
          //       type: "final",
          //     },
          //   },
          // },
        },
        onDone: "dealWithPossibilities",
      },
      dealWithPossibilities: {
        always: [
          {
            target: "welcomeInitializeApplication",
            cond: (ctx) => !ctx.remoteConfiguration,
          },
          {
            target: "welcomeInitializeVaultOnly",
            cond: (ctx) => ctx.remoteConfiguration && !ctx.vault,
          },
          {
            target: "allGood",
            cond: (ctx) => ctx.remoteConfiguration && ctx.vault,
          },
        ],
      },
      welcomeInitializeApplication: {
        on: {
          SUBMIT_WELCOME: "initializeVault",
        },
      },
      welcomeInitializeVaultOnly: {
        on: {
          SUBMIT_WELCOME: "initializeVault",
        },
      },
      initializeVault: {
        initial: "idle",
        states: {
          idle: {
            on: {
              SUBMIT_CREDENTIALS: "authenticating",
            },
          },
          authenticating: {
            entry: assign({ error: (_context, _event) => undefined }),
            invoke: {
              src: "authenticate",
              onDone: { target: "validatingAuthorization", actions: "setCredentialsAndProfile" },
              onError: { target: "idle", actions: "onError" },
            },
          },
          validatingAuthorization: {
            invoke: {
              src: "validateAuthorization",
              onDone: { target: "authenticated" },
              onError: { target: "idle", actions: "onError" },
            },
          },
          authenticated: {
            type: "final",
          },
        },
        onDone: "initializeRepositories",
      },
      initializeRepositories: {
        initial: "checkingIfGoNext",
        states: {
          checkingIfGoNext: {
            always: [
              {
                target: "done",
                cond: (context, _event) => {
                  return context.remoteConfiguration !== undefined;
                },
              },
              {
                target: "idle",
                cond: (context, _event) => {
                  return context.remoteConfiguration === undefined;
                },
              },
            ],
          },
          idle: {
            on: {
              SUBMIT_REPOSITORY: "validateRepository",
            },
          },
          validateRepository: {
            entry: assign({
              remoteConfiguration: (_context, event: any) => {
                console.log(event);
                return defaultConfiguration({ url: event.repositoryUrl });
              },
              error: (_context, _event) => undefined,
            }),
            invoke: {
              src: "validateAuthorization",
              onDone: { target: "cloneRespositories" },
              onError: { target: "idle", actions: "onError" },
            },
          },
          cloneRespositories: {
            initial: "start",
            states: {
              start: {
                invoke: {
                  src: "cloneRepositories",
                  onDone: { target: "done" },
                  onError: {},
                },
              },
              done: {
                type: "final",
              },
            },
            onDone: "done",
          },
          done: {
            type: "final",
          },
        },
        onDone: "allGood",
      },
      allGood: {
        type: "final",
      },
    },
  },
  {
    actions: {
      setRemoteConfiguration: assign({
        remoteConfiguration: (_ctx, event: any) => {
          return event.data;
        },
      }),
      setVault: assign({
        vault: (_ctx, event: any) => {
          return event.data;
        },
      }),
      setCredentialsAndProfile: assign({
        credentials: (_ctx, event: any) => {
          log.debug("setAuthenticationAndProfile", event);
          return event.data.authentication;
        },
        profile: (_ctx, event: any) => {
          log.debug("setAuthenticationAndProfile", event);
          return event.data.profile;
        },
      }),
      onError: assign({
        error: (_ctx, event: any) => {
          log.debug("Something happened:", event.data.message);
          return event.data.message;
        },
      }),
    },
    services: {
      initializeDirectories: async (_ctx, _event) => initializeDirectories(),
      checkRemoteConfiguration: async (_ctx, _event) => fetchRepositoryConfiguration(),
      checkVault: async (_ctx, _event) => getVault(),
      checkLocalRepositoriesConfiguration: async (_ctx, _event) =>
        getLocalRepositoriesConfiguration(),
      authenticate: async (_ctx, event: any) => {
        const { type: _type, ...authentication } = event;
        try {
          const profile = await getProfile({ token: authentication.token });

          return {
            profile,
            authentication,
          };
        } catch (error) {
          throw new Error("setup.authentication.errors.authentication_failed");
        }
      },
      validateAuthorization: async ({ remoteConfiguration, credentials }, _event: any) => {
        if (remoteConfiguration === undefined) {
          return;
        }

        log.debug("validateAuthorization", remoteConfiguration);
        // Validate Authorization
        const isAuthorized = await isAuthorizedToPush({
          configuration: remoteConfiguration,
          token: credentials.token,
        });

        if (!isAuthorized) {
          throw new Error("setup.authentication.errors.not_authorized");
        }
      },
      cloneRepositories: async (ctx, _event) => {
        if (!ctx.remoteConfiguration) {
          return;
        }
        await initializeDirectories(true);
        debugger;
        await cloneRepositores({
          configuration: ctx.remoteConfiguration,
          credentials: ctx.credentials,
        });
      },
    },
  }
);

export const setupService = interpret(setupMachine, {
  //@ts-ignore
  devTools: !window.Cypress,
}).start();
