interface Profile {
  id: number;
  username: string;
  name: string;
  avatar_url: string;
  email: string;
}

export const getProfile = async ({ token }: getProfileParams): Promise<Profile> => {
  const api = new Gitlab({
    token: token,
  });

  const user = (await api.Users.current()) as any;

  log.debug({ user });
  return {
    id: user.id,
    username: user.username,
    name: user.name,
    avatar_url: user.avatar_url,
    email: user.email,
  } as Profile;
};

// gitlab.com/gitlab-org/gitlab-foss/-/blob/master/lib/gitlab/path_regex.rb#L137
export const extractProjectNameFromUrl = ({ url }: any): string | undefined => {
  const parts = url
    .split(".git")[0]
    .match(
      /https?:\/\/.*\/((?:[a-zA-Z0-9_.][a-zA-Z0-9_\-.]*[a-zA-Z0-9_-]|[a-zA-Z0-9_])\/(?:[a-zA-Z0-9_.][a-zA-Z0-9_\-.]*))$/
    );

  console.error(parts);
  if (parts?.length > 1) {
    return parts[parts.length - 1];
  }

  return undefined;
};

export const isAuthorizedToPush = async ({
  configuration,
  token,
}: {
  configuration: Configuration;
  token: string;
}) => {
  console.log({ configuration, token });

  const projectName = extractProjectNameFromUrl({ url: configuration.sourceRepository.url });
  if (!projectName) return false;

  log.debug({ projectName });
  const api = new Gitlab({
    token,
  });

  const project = await api.Projects.show(projectName);
  //@ts-ignore
  const permissions = project.permissions;

  log.debug({ project, permissions });

  const hasProjectAccess = !!permissions.project_access;
  const hasProjectAccessLevel = (permissions.project_access?.access_level || 0) >= 30;
  const hasGroupAccessLevel = (permissions.group_access?.access_level || 0) >= 30;

  return hasProjectAccess ? hasProjectAccessLevel : hasGroupAccessLevel;
};
